body {
  background-color: #282c34;
  padding: 20px;
  margin: 0; }

h1, h2, p, ul, li {
  font-family: sans-serif; }

ul.header li {
  display: inline;
  list-style-type: none;
  margin: 0; }

ul.header {
  background-color: #111;
  padding: 0; }

ul.header li a {
  color: #FFF;
  font-weight: bold;
  text-decoration: none;
  padding: 20px;
  display: inline-block; }

.active {
  background-color: #0099FF; }

.content {
  background-color: #FFF;
  padding: 20px; }

.content h2 {
  padding: 0;
  margin: 0;
  font-size: 30px;
  text-align: center; }

.content li {
  margin-bottom: 10px; }

.vrOneWorldTitle {
  color: #FFF; }

.homeHeader img {
  position: relative;
  margin-left: 10px;
  margin-bottom: 10px;
  width: 120px;
  height: 100px; }

.homeHeader h1 {
  display: inline;
  text-align: center;
  border: 3px solid #828282;
  border-radius: 10px;
  box-shadow: 4px 4px 4px #828282; }

header.homeHeader {
  text-align: center;
  letter-spacing: 1px;
  height: 5em;
  padding: 2em 10%; }

.figure {
  vertical-align: middle; }

button {
  border-radius: 2px;
  min-width: 88px;
  background-color: #FA6900;
  cursor: pointer;
  border: 0;
  min-width: 120px;
  color: #fff;
  font-size: 20px;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.26);
  padding: 10px 6px; }

.theRules p {
  font-size: 20px; }

.theMission p {
  font-size: 20px; }

.contact p {
  font-size: 20px; }

.UserForm {
  margin-top: 25px;
  display: inline-block;
}
.ChatOpenButton {
  margin-top: 15px;
}
.RulesBody {
    text-align: center;
    font-size: 20px;
}

.all-channel__logo {
    height: 1000px;
    width: 100%;
}

.all_chat {
    margin-top: 25px;
}

.messageForm body {
  font-family: 'Open Sans', sans-serif;
  background-color: #fff;
}

.messageForm header {
  color: #fff;
  background-color: #103D5D;
  padding: 33px 0 25px 15px;
}

.messageForm header h1 {
  font-size: 25px;
  font-weight: 600;
  letter-spacing: 0.3px;
  display: inline-block;
  margin: 0;
  vertical-align: middle;

}

.fa {
  font-size: 22px;
  -webkit-transform: translateY(50deg);
          transform: translateY(50deg);
}

.messageForm header i {
  vertical-align: middle;
  margin-left: 12px;
}

.container {
  display: flex;
  max-width: 1100px;
  margin: 0 auto;
  padding-top: 30px;
}

.wrapper {
  max-width: 1100px;
  margin: 0 auto;
}

.add-post {
  border-radius: 2px;
  border-top: 3px solid #103D5D;
  margin-right: 50px;
  background-color: rgb(237, 237, 237);
  padding: 60px 18px;
  box-shadow: 0 3px 4px 0 rgba(0,0,0,.14), 0 3px 3px -2px rgba(0,0,0,.12), 0 1px 8px 0 rgba(0,0,0,.2);
  margin-bottom: 34px;
  width: 25%;
  margin-right: auto;
}

.display-post {
  width: 65%;
  margin-left: 50px;
}

.display-post button {
  margin-top: 20px;
  margin-left: auto;
  display: block;
}

.add-post form {
  width: 100%;
  overflow: hidden;
}

.messageForm input {
  font-size: 22px;
  color: #000;
  padding: 18px 22px;
  font-size: 16px;
  margin-bottom: 17px;
  border: 0;
  display: block;
  width: 100%;
}

.messageForm ul {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
}

.messageForm li {
  width: calc(50% - 50px);
  list-style-type: none;
  margin-right: 50px;
  background-color: rgb(237, 237, 237);
  color: #000;
  margin-bottom: 30px;
  box-shadow: 0 3px 4px 0 rgba(0,0,0,.14), 0 3px 3px -2px rgba(0,0,0,.12), 0 1px 8px 0 rgba(0,0,0,.2);
}

.messageForm h2 {
  padding: 0;
  margin: 0;
  font-weight: 400;
}

.messageForm li h3 {
  background-color: #103D5D;
  margin: 0;
  color: #fff;
  font-weight: 300;
  padding: 15px;
}

.messageForm li p {
  padding: 15px;
}

.messageForm form button {
  width: 100%;
  margin-top: 10px;
}

.messageForm button {
  border-radius: 2px;
  min-width: 88px;
  background-color: #FA6900;
  cursor: pointer;
  border: 0;
  min-width: 120px;
  color: #fff;
  font-size: 14px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,.26);
  padding: 10px 6px;
}

